import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import FormMutasiBarang from "./Component/FormMutasiBarang";
import axios from "axios";
import { getListMutasiBarang } from "../actions/mutasiBarangAction";
import Swal from "sweetalert2";
import { connect } from "react-redux";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import { getUUID } from "../actions/fakturAction";
import {
  getBruto,
  getGross,
  getNetto,
  getPkg,
  getQty,
} from "../actions/LogicAction";
import Axios from "axios";
import PrintNota from "./Component/PrintNota";
import { warning } from "../Alert";

const columns = [
  {
    dataField: "Lihat Detail",
    isDummyField: true,
    text: "Lihat Detail",
    formatter: (row) => {
      return (
        <span>
          <i className="fa fa-chevron-down"></i>
        </span>
      );
    },
  },
  {
    dataField: "kodeJenis01",
    text: "Kode Barang",
  },
  {
    dataField: "keterangan",
    text: "Keterangan",
  },
  {
    dataField: "kadarTransaksi01",
    text: "Kat Harga",
  },
  {
    dataField: "kadarCetak01",
    text: "Kadar",
  },
  {
    dataField: "pkg01",
    text: "Pkg (Gr)",
  },
  {
    dataField: "bruto01",
    text: "Bruto (Gr)",
  },
  {
    dataField: "kadarTransaksi01",
    text: "Harga",
  },
  {
    dataField: "netto01",
    text: "Netto (Gr)",
  },
];

const expandRow = {
  renderer: (row) => (
    <div>
      <p>{`Tujuan`}</p>
      <p>{`Kode Tujuan : ${row.kodeJenis02} | Keterangan : ${row.keteranganTujuan} | Kat Harga : ${row.kadarTransaksi02} | Kadar : ${row.kadarCetak02} | Pkg(Gr): ${row.pkg02}`}</p>
      <p>{`Bruto(Gr) : ${row.bruto02} | Harga Tujuan : ${row.kadarTransaksi02} | Netto : ${row.netto02}`}</p>
    </div>
  ),
};

const maptostate = (state) => {
  return {
    listBarang: state.provinsi.mutasiBarang,
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    sumBruto: state.perhitungan.sumBruto,
    sumNetto: state.perhitungan.sumBruto,
    sumPkg: state.perhitungan.sumPkg,
    sumQty: state.perhitungan.sumQty,
    sumGross: state.perhitungan.sumGross,
  };
};

const server = process.env.REACT_APP_BACKEND_URL;
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class MutasiBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lokasi: [],
      listKategori: [
        {
          nama: "AMBIL BARANG",
        },
        {
          nama: "KEMBALI STOK",
        },
        {
          nama: "KOREKSI STOK",
        },
        {
          nama: "MUTASI BAHAN",
        },
        {
          nama: "PENGAMBILAN SALES",
        },
        {
          nama: "PINDAH",
        },
        {
          nama: "PINDAH GUDANG REPARASI",
        },
        {
          nama: "RETURN SALES",
        },
        {
          nama: "SALAH INPUT",
        },
        {
          nama: "SALAH PINDAH",
        },
        {
          nama: "STOK AWAL",
        },
        {
          nama: "TAMBAH BARANG",
        },
      ],
      kodeJenis: "",
      kodeJenisTujuan: "",
      noMutasi: "",
      kodeLokasi: "",
      tanggal: "",
      keterangan: "",
      kategori: "",
      noBon: "",
    };
  }

  componentDidMount() {
    axios
      .get(server + "storages2?tipeData=LOKASI")
      .then((res) =>
        this.setState({
          lokasi: res.data,
        })
      )
      .catch((err) => console.log(err));
    this.props.dispatch(getListMutasiBarang());
    this.props.dispatch(getUUID());
    this.props.dispatch(getBruto("mutasiBarang"));
    this.props.dispatch(getNetto("mutasiBarang"));
    this.props.dispatch(getPkg("mutasiBarang"));
    this.props.dispatch(getQty("mutasiBarang"));
    this.props.dispatch(getGross("mutasiBarang"));
  }

  handleSubmit(data) {
    var supp = data.kodeBarang
      ? data.kodeBarang
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
    });
    var supp1 = data.kodeBarangTujuan
      ? data.kodeBarangTujuan
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result = supp1.split("|");
    this.setState({
      kodeJenisTujuan: result[0],
    });
    var barang1 = JSON.parse(localStorage.getItem("mutasiBarang")) || [];
    var simpan = {
      noFaktur: this.props.noFaktur,
      bruto01: parseFloat(data.bruto).toFixed(3),
      bruto02: parseFloat(data.brutoTujuan).toFixed(3),
      kadarCetak01: parseFloat(data.kadar).toFixed(3),
      kadarCetak02: parseFloat(data.kadarTujuan).toFixed(3),
      kadarTransaksi01: parseFloat(data.kadarTransaksi).toFixed(3),
      kadarTransaksi02: parseFloat(data.kadarTransaksiTujuan).toFixed(3),
      keterangan: data.keterangan,
      keteranganTujuan: data.keteranganTujuan,
      netto01: parseFloat(data.netto).toFixed(3),
      netto02: parseFloat(data.nettoTujuan).toFixed(3),
      pkg01: parseFloat(data.pkg).toFixed(3),
      pkg02: parseFloat(data.pkgTujuan).toFixed(3),
      qty01: 1,
      qty02: 1,
      gross01: (parseFloat(data.bruto) + parseFloat(data.pkg)).toFixed(3),
      gross02: (
        parseFloat(data.brutoTujuan) + parseFloat(data.pkgTujuan)
      ).toFixed(3),
      kodeJenis01: result_explode[0],
      kodeJenis02: result[0],
      kodeKategori01: "-",
      kodeKategori02: "-",
      k01: 0,
      k02: 0,
      p01: 0,
      p02: 0,
    };
    barang1.push(simpan);
    localStorage.setItem("mutasiBarang", JSON.stringify(barang1));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    })
      .then(() => this.props.dispatch(reset("mutasiBarang")))
      .then(() => this.props.dispatch(getListMutasiBarang()));
  }
  onChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submitMutasiBarang() {
    if (this.state.kodeLokasi === "") {
      warning("Mohon isi Lokasi");
      return false;
    } else if (this.state.keterangan === "") {
      warning("Mohon Isi Keterangan");
      return false;
    } else if (this.state.kategori === "") {
      warning("Mohon Isi Kategori");
      return false;
    } else if (this.state.noBon === "") {
      warning("Mohon Isi Nomor Bon");
      return false;
    } else if (this.state.tanggal === "") {
      warning("Mohon Isi Tanggal");
      return false;
    } else if (localStorage.getItem("mutasiBarang") === null) {
      warning("Mohon Tambahkan Barang Yang akan dipindahkan");
      return false;
    }
    var simpan = {
      tanggal: this.state.tanggal,
      noFaktur: this.props.noFaktur,
      noBon: this.state.noBon,
      kategoriTransaksi: this.state.kategori,
      keterangan: this.state.keterangan,
      kodeLokasi: this.state.kodeLokasi,
      tipeFaktur: "-",
      barangDetailList: JSON.parse(localStorage.getItem("mutasiBarang")),
      footerItem: {
        totalBruto: 0,
        totalNetto: 0,
        totalQty: 0,
        totalGross: 0,
        totalK: 0,
        totalP: 0,
        totalpkg: 0,
        totalNettoExt: 0,
        totalRp: 0,
      },
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    console.log(JSON.stringify(simpan));

    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("mutasiBarang"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        i++,
        ticket.kodeJenis01,
        ticket.bruto01,
        ticket.kadarTransaksi01,
        ticket.netto01,
        ticket.kodeJenis02,
        ticket.bruto02,
        ticket.kadarTransaksi02,
        ticket.netto02,
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    console.log(tableRows);
    var columTable = [
      "No",
      "Kode Awal",
      "Bruto",
      "Harga (%)",
      "Netto Awal",
      "Kode Tujuan",
      "Harga (%)",
      "Netto Akhir",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(serverNonMaster + "item/dc/mutasibarang", simpan)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Transaksi Berhasil",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() =>
            PrintNota(
              "Tanggal",
              this.state.tanggal,
              "",
              "",
              "Nomor Bon",
              this.state.noBon,
              "",
              "",
              "ADMIN",
              this.state.tanggal,
              "",
              columTable,
              "MUTASI BARANG",
              tableRows
            )
          )
          .then(() => localStorage.removeItem("mutasiBarang"))
          .then(() => this.props.dispatch(getListMutasiBarang()))
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Sepertinya ada kendala, silahkan cek koneksi",
          showConfirmButton: false,
          timer: 1500,
        }).then(() => console.log(err))
      );
    this.props.dispatch(getListMutasiBarang());
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h5 className="card-title mb-3" style={{ fontSize: 30 }}>
                  Mutasi Barang
                </h5>
                <div className="row">
                  <div className="col-md-4 form-group">
                    <label>Nomor Mutasi</label>
                    <input
                      type="text"
                      id="noMutasi"
                      name="noMutasi"
                      className="form-control"
                      readOnly
                      onChange={this.onChange("noMutasi")}
                      value={this.props.noFaktur}
                    />
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Tanggal</label>
                    <input
                      type="date"
                      id="tanggal"
                      name="tannggal"
                      className="form-control"
                      onChange={this.onChange("tanggal")}
                    />
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Bon Mutasi</label>
                    <input
                      type="text"
                      id="bonMutasi"
                      name="bonMutasi"
                      className="form-control"
                      onChange={this.onChange("noBon")}
                    />
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Kategori</label>
                    <select
                      id="kategori"
                      name="kategori"
                      className="form-control"
                      onChange={this.onChange("kategori")}
                    >
                      <option value="DEFUALT" key="DEFUALT">
                        SILAHKAN PILIH
                      </option>
                      {this.state.listKategori.map((list) => (
                        <option value={list.nama} key={list.nama}>
                          {list.nama}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Keterangan</label>
                    <input
                      type="text"
                      id="keterangan"
                      name="keterangan"
                      className="form-control"
                      onChange={this.onChange("keterangan")}
                    />
                  </div>
                  <div className="col-md-4 form-group">
                    <label>Lokasi</label>
                    <select
                      type="text"
                      id="lokasi"
                      name="lokasi"
                      className="form-control"
                      onChange={this.onChange("kodeLokasi")}
                    >
                      <option value="DEFAULT" key="DEFAULT">
                        SILAHKAN PILIH
                      </option>
                      {this.state.lokasi.map((lokasi) => (
                        <option
                          value={lokasi.kodeLokasi}
                          key={lokasi.kodeLokasi}
                        >
                          {lokasi.namaLokasi}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div className="col-lg-4 col-md-4 mb-3">
                    <div className="text-left">
                      <button
                        className="btn btn-primary"
                        onClick={() => this.submitMutasiBarang()}
                      >
                        SIMPAN
                      </button>
                      <button
                        className="btn btn-primary ml-2"
                        data-toggle="modal"
                      >
                        BATAL
                      </button>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-4 mb-3">
                    <div className="text-center">
                      <button className="btn btn-primary" data-toggle="modal">
                        BARCODE (+)
                      </button>
                      <button
                        className="btn btn-primary ml-2"
                        data-toggle="modal"
                      >
                        BARCODE (-)
                      </button>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-4 mb-3">
                    <div className="text-right">
                      <button
                        className="btn btn-primary"
                        data-toggle="modal"
                        data-target="#primary-header-modal"
                      >
                        TAMBAH BARANG
                      </button>
                    </div>
                  </div>
                  {this.props.listBarang ? (
                    <BootstrapTable
                      keyField="id"
                      data={this.props.listBarang}
                      columns={columns}
                      bordered={true}
                      headerClasses="header-table"
                      expandRow={expandRow}
                    />
                  ) : (
                    <Skeleton width="100%" height={50} />
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Mutasi Barang
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormMutasiBarang
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(MutasiBarang);
